import { createWebHistory, createRouter } from "vue-router";
import HomeInfo from "./components/HomeInfo.vue";
import PostItem from "./components/PostItem.vue";
import PostDetails from "./components/PostDetails.vue";
import PostDetailsAuthorInfo from "./components/PostDetailsAuthorInfo.vue";
import PostDetailsComment from "./components/PostDetailsComment.vue";

const routes = [
  {
    path: "/",
    component: HomeInfo,
  },
  {
    path: "/list",
    component: PostItem,
  },
  {
    path: "/details/:id",
    component: PostDetails,
    children: [{
      path: '',
      components: {
        PostDetailsComment: PostDetailsComment,
        PostDetailsAuthorInfo: PostDetailsAuthorInfo,
      }
    }]
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
